@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700;800;900&family=Source+Sans+Pro:wght@200;300;400;600;700;900&family=Teko:wght@300;400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sidebar .nav-link{color:#001d3d;text-transform:capitalize;font-weight:600;font-size:clamp(1.1rem,5vw,1.3rem);transition:.3s ease-in-out;-webkit-transition:.3s ease-in-out;-moz-transition:.3s ease-in-out;-ms-transition:.3s ease-in-out;-o-transition:.3s ease-in-out}.sidebar .nav-link:hover{color:#ff4500}
.footer{background-color:#0f1d34;padding:150px 0;margin-bottom:0}.icon-box a{position:relative;display:inline-block;background-color:#fff;margin:5px;width:35px;height:35px;border-radius:8px;-webkit-border-radius:8px;-moz-border-radius:8px;-ms-border-radius:8px;-o-border-radius:8px}.icon-box a:hover{background-color:#007bff}.icon{font-size:20px;color:#000;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);-webkit-transform:translate(-50%, -50%);-moz-transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);-o-transform:translate(-50%, -50%)}
#about img{height:650px;width:80%}#about .progress-list{margin:30px 0 0}#about .progress-list label{text-transform:capitalize;font-size:1.2rem}#about .progress{margin:0 0 20px 0}@media(max-width: 776px){#about img{width:100%}}
#appointment{background-image:url(/static/media/appointment-bg.f9d79b11.jpg);background-size:cover;background-position:center;background-repeat:no-repeat}#appointment .overlay{width:100%;height:100%;background:rgba(0,0,0,.7)}#appointment img{height:600px;margin-top:-140px}#appointment .appoint-content{color:#edf2f4}@media(max-width: 991px){#appointment img{height:400px;margin-top:0}}@media(max-width: 767px){#appointment .col-md-5 img{display:none}#appointment .col-md-6{padding:30px;text-align:center}}
.info-primary{background:#006d77}.info-secondary{background:#283618}.info-card{height:150px;padding:25px}.info-card p{margin:0}
.form-section{background-image:url(/static/media/appointment-bg.f9d79b11.jpg);background-position:center;background-repeat:no-repeat;background-size:cover}.overlay{top:0;left:0;height:100%;width:100%;background-color:rgba(0,0,0,.8)}
.header{height:650px;background:#001d3d}
.hero{height:100%;display:flex;justify-content:space-around;align-items:center}.hero h1{margin:0 0 15px 0;color:#fff}.hero p{color:#fff}.hero .btn{font-size:1.2rem;letter-spacing:1px}@media(max-width: 1099px){.hero{margin:0 50px}}@media(max-width: 991px){.hero{height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center}.hero img{display:none}}
.navigation{background:#001d3d}.navigation .navbar-brand{color:#fff;font-size:2.1rem;font-weight:700}.navigation .nav-link{color:#fff;text-transform:capitalize;font-size:1.2rem;font-weight:300;margin:0 10px;letter-spacing:1px;display:inline-block}.navigation .nav-link:hover{color:orange}.navigation .btn:hover{color:#fff !important}.navigation .navbar-toggler{border:1px solid #fff}
*{margin:0;padding:0;outline:0;box-sizing:border-box;scroll-behavior:smooth;list-style:none;text-decoration:none}html{overflow-x:hidden}body{font-family:"Teko",sans-serif;font-family:"Playfair Display",serif;font-family:"Source Sans Pro",sans-serif}h1,h2{font-family:"Teko",sans-serif;font-weight:700;text-transform:capitalize;margin:10px 0 25px}h3,h4,h5,h6{font-family:"Teko",sans-serif;font-weight:700;text-transform:capitalize}h1{font-size:clamp(2.2rem,5vw,3.2rem)}h2{font-size:clamp(1.8rem,5vw,2.7rem)}.hm-btn{font-family:"Playfair Display",serif;text-transform:capitalize;padding:12px 18px;border-radius:8px;-webkit-border-radius:8px;-moz-border-radius:8px;-ms-border-radius:8px;-o-border-radius:8px}.div-spacing{margin-bottom:120px}
